body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .dark-mode {
    background-color: black;
  }
  .dark-mode h1 {
    color: white;
  }
  .container {
    max-width: 960px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
    min-height: 100vh;
  }
  .notes-list {
    display: grid;
    grid-gap: 1rem;
    /* 1fr es una fraccion, es decir, que una columna no podra ser mas grande que 1 fraccion */
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  .note.new {
    background-color: #67d7cc;
  }
  
  textarea {
    border: none;
    /* resize para que no se pueda expandir el text area */
    resize: none;
    background-color: #67d7cc;
  }
  
  /* para que cuando pulses en el textarea no se marque la casilla */
  textarea:focus {
    outline: none;
  }
  .save {
    background-color: #36a6d3;
    border: none;
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
    float: right;
    width: 100px;
  }
  .save:hover {
    background-color: #1428db;
    cursor: pointer;
  }
  
  .note {
    background-color: #fef68a;
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: pre-wrap;
    /* para que el texto no sobrepase el limite de width de la caja */
  
    word-wrap: break-word;
  }
  .note-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .delete-icon {
    cursor: pointer;
  }
  
  .search {
    display: flex;
    align-items: center;
    background-color: rgb(233, 233, 233);
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 1.5rem;
  }
  
  .search ::placeholder {
    color: black;
    width: 100px;
  }
  
  .search input {
    border: none;
    background-color: rgb(233, 233, 233);
  }
  
  /* quitar las lineas del input al pulsarlo */
  .search input:focus {
    outline: none;
  }
  